"use client"
import { getRestaurantsFiltered } from '@/api/auth';
import { useAppDispatch } from '@/hooks/reduxHooks';
import { Category, Restaurant } from '@/redux/reduxTypes';
import { getRestaurants } from '@/redux/restaurantSlice';
import { ChevronRight, Clock, Star } from 'lucide-react';
import Image from 'next/image';
import Link from 'next/link';
import React, { useEffect, useState } from 'react'




const RestaurantsByCategory = () => {
    const [isActive, setIsActive] = useState<Category>(Category.All);
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const dispatch = useAppDispatch();
    const {}
    const fetchRestaurants = async (categorie: string) => {
        try {
            setIsLoading(true);

            const info = await getRestaurantsFiltered(isActive);
            if (info)
                dispatch(getRestaurants(info));
        } catch (err) {
            console.error(err);
        } finally {
            setIsLoading(false);
        }

    };
    useEffect(()=>{
        fetchRestaurants(isActive);
    },[])

    return (
        <section className='mb-16'>
            <h1 className='section-title mb-[22px]'>Browse by Category</h1>
            <div className="flex gap-4 items-center mb-[60px]">
                {Object.values(Category).map((categorie, index) => {
                    return (
                        <button key={index} onClick={async () => { setIsActive(categorie); console.log(categorie); await fetchRestaurants(categorie); }} className={`h-10 cursor-pointer transition-all   flex items-center justify-center px-3 font-medium text-sm leading-[22px]  rounded-[30px] border-[1px] border-borderColor ${isActive === categorie ? "bg-primary text-white" : "bg-transparent"} `} >{categorie}</button>
                    )
                })}
            </div>
            {isLoading
                ?
                (<div className="animate-spin rounded-full h-12 w-12 border-t-4 border-blue-500 border-solid mx-auto"></div>) :
                (
                     isActive === Category.All ? (<>
                        <div className="flex justify-between items-center">

                            <h1 className='section-title mb-[22px] '>Top-Rated Restaurants</h1>
                            <Link href={"#"} className="flex items-center gap-2 leading-[22px] font-medium text-primary group">
                                <span className='group-hover:text-black transition-colors '>View All</span>
                                <ChevronRight className='group-hover:text-black transition-colors ' size={16} />
                            </Link>

                        </div>
                        <div className="grid grid-cols-4 gap-x-6">
                            {restaurants.sort((a, b) => a.rating - b.rating).map((restaurant, index) => {
                                return (
                                    <div key={index} className="shadow-xs rounded-lg border-[1px] overflow-hidden border-borderColor ">
                                        <div className="mb-5  h-40 w-full overflow-hidden">

                                            <img src={restaurant.imageUrl} alt="Restaurant" />
                                        </div>
                                        <div className="pb-[17px] pr-[17px] pl-5 flex flex-col gap-3">
                                            <div className="flex flex-col gap-[2px]">
                                                <h3 className='text-[18px] font-semibold leading-7'>{restaurant.title}</h3>
                                                <p className='text-sm leading-5 text-gray'>{restaurant.description}</p>
                                            </div>
                                            <div className="flex justify-between items-center mb-1">
                                                <div className="flex items-center gap-1 text-amber-400">
                                                    <Star size={14} />
                                                    <span>{restaurant.rating}</span>
                                                </div>
                                                <div className='text-sm leading-5 text-gray flex items-center gap-1'>
                                                    <Clock size={14} />
                                                    <span>15-25 min</span>
                                                </div>
                                            </div>
                                            <button className='btn-transparent gap-2 h-10 group'>
                                                <span>View Menu</span>
                                                <ChevronRight className='group-hover:rotate-90 transition-transform' size={14} />
                                            </button>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </>
                    ) : (
                        <div className="grid grid-cols-4 gap-x-6">
                            {restaurants.map((restaurant, index) => {
                                return (
                                    <div key={index} className="shadow-xs rounded-lg border-[1px] overflow-hidden border-borderColor ">
                                        <div className="mb-5  h-40 w-full overflow-hidden">
                                            <img src={restaurant.imageUrl} alt="Restaurant" />
                                        </div>
                                        <div className="pb-[17px] pr-[17px] pl-5 flex flex-col gap-3">
                                            <div className="flex flex-col gap-[2px]">
                                                <h3 className='text-[18px] font-semibold leading-7'>{restaurant.title}</h3>
                                                <p className='text-sm leading-5 text-gray'>{restaurant.description}</p>
                                            </div>
                                            <div className="flex justify-between items-center mb-1">
                                                <div className="flex items-center gap-1 text-amber-400">
                                                    <Star size={14} />
                                                    <span>{restaurant.rating}</span>
                                                </div>
                                                <div className='text-sm leading-5 text-gray flex items-center gap-1'>
                                                    <Clock size={14} />
                                                    <span>15-25 min</span>
                                                </div>
                                            </div>
                                            <button className='btn-transparent gap-2 h-10 group'>
                                                <span>View Menu</span>
                                                <ChevronRight className='group-hover:rotate-90 transition-transform' size={14} />
                                            </button>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    )
            )
            }

        </section>
    )
}

export default RestaurantsByCategory